import React, { useState } from "react";
import { Link } from "gatsby";
import Lightbox from "react-image-lightbox";

import "react-image-lightbox/style.css";
import productImg1 from "../../assets/images/projectGallery/image 1-min.png";
import productImg2 from "../../assets/images/projectGallery/image 2-min.png";
import productImg3 from "../../assets/images/projectGallery/image 3-min.png";
import productImg4 from "../../assets/images/projectGallery/image 4-min.png";
import productImg5 from "../../assets/images/projectGallery/image 5-min.png";
import productImg6 from "../../assets/images/projectGallery/image 6-min.png";
import productImg7 from "../../assets/images/projectGallery/image 7-min.png";
import productImg8 from "../../assets/images/projectGallery/image 8-min.png";
import productImg9 from "../../assets/images/projectGallery/image 9-min.png";
import productImg10 from "../../assets/images/projectGallery/image 10-min.png";
import productImg11 from "../../assets/images/projectGallery/image 11-min.png";
import productImg12 from "../../assets/images/projectGallery/image 12-min.png";
import productImg13 from "../../assets/images/projectGallery/image 13-min.png";
import productImg14 from "../../assets/images/projectGallery/image 14-min.png";
import productImg15 from "../../assets/images/projectGallery/image 15-min.png";
import productImg16 from "../../assets/images/projectGallery/image 17-min.png";
import productImg17 from "../../assets/images/projectGallery/image 18-min.png";
import productImg18 from "../../assets/images/projectGallery/image 19-min.png";
import productImg19 from "../../assets/images/projectGallery/image 20-min.png";
import productImg20 from "../../assets/images/projectGallery/image 21-min.png";
import productImg21 from "../../assets/images/projectGallery/image 16-min.png";

import "./projectGallery.css";
import CallToAction from "../master/cta";

const images = [
  productImg1,
  productImg2,
  productImg3,
  productImg4,
  productImg5,
  productImg6,
  productImg7,
  productImg8,
  productImg9,
  productImg10,
  productImg11,
  productImg12,
  productImg13,
  productImg14,
  productImg15,
  productImg16,
  productImg17,
  productImg18,
  productImg19,
  productImg20,
  productImg21,
];

const ProjectGallery = () => {
  const [open, setOpen] = useState(undefined);
  return (
    <div className="home-content">
      <div className="header-title">
        <div className="container">
          <div className="header-title-text">
            <h2>
              Our <span>Gallery</span>
            </h2>
            <ul>
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <Link to="/gallery">/ Gallery</Link>
              </li>
              <li>/ Project Gallery</li>
            </ul>
          </div>
        </div>
      </div>
      <div className="productGallery-section">
        <div className="container">
          <div className="productGallery-grid-section">
            {images.map((img, i) => (
              <div className="productGallery-box" key={i}>
                <img src={img} alt="galleryImg" onClick={() => setOpen(i)} />{" "}
              </div>
            ))}

            {typeof open !== "undefined" && (
              <Lightbox
                mainSrc={images[open]}
                nextSrc={images[(open + 1) % images.length]}
                prevSrc={images[(open + images.length - 1) % images.length]}
                onCloseRequest={() => setOpen()}
                onMovePrevRequest={() => {
                  console.log("open =>", open);
                  setOpen((open) => (open + images.length - 1) % images.length);
                }}
                onMoveNextRequest={() => {
                  console.log("open =>moce", open);
                  setOpen((open) => (open + 1) % images.length);
                }}
              />
            )}
          </div>
        </div>
      </div>
      <CallToAction />
    </div>
  );
};

export default ProjectGallery;
